@use '../util/' as *;

h1, h2, h3, h4, h5 {
    line-height: 1.05;
    margin-top: 0;
}

h1 {
    font-size: rem(40);
    margin-bottom: rem(16);
    @include breakpoint(large) {
        font-size: rem(56);
    }
}

p {
    font-size: rem(15);
}

ul {
    list-style: none;
}

a, 
a:visited, 
a:active {
    text-decoration: none;
}