:root {
  --soft-orange: hsl(35, 77%, 62%);
  --soft-red: hsl(5, 85%, 63%);
  --off-white: hsl(36, 100%, 99%);
  --greyish-blue: hsl(233, 8%, 79%);
  --dark-grayish-blue: hsl(236, 13%, 42%);
  --very-dark-blue: hsl(240, 100%, 5%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  background-color: var(--off-white);
  font-family: var(--font-inter);
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1, h2, h3, h4, h5 {
  line-height: 1.05;
  margin-top: 0;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}
@media (min-width: 47.9375em) {
  h1 {
    font-size: 3.5rem;
  }
}

p {
  font-size: 0.9375rem;
}

ul {
  list-style: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 1.5rem auto 2.875rem;
  max-width: 69.375rem;
  padding: 0 1.25rem;
  height: 2.5rem;
}
@media (min-width: 90em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: space-between;
}
.header img {
  height: 2.5rem;
  margin: 0;
  padding: 0;
}

.nav {
  padding: 0;
  margin: 0;
}
.nav__checkbox {
  cursor: pointer;
  height: 2rem;
  width: 2rem;
  margin: 0.3125rem auto;
  position: absolute;
  opacity: 0;
  z-index: 3;
}
.nav__checkbox:checked ~ .nav__container {
  transform: translate(-100%);
}
.nav__checkbox:checked ~ .nav__toggle .nav__line:first-child {
  transform: rotate(45deg) translate(-3px, -3px);
}
.nav__checkbox:checked ~ .nav__toggle .nav__line:nth-child(2) {
  opacity: 0;
}
.nav__checkbox:checked ~ .nav__toggle .nav__line:nth-child(3) {
  transform: rotate(-45deg) translate(0, -1px);
}
@media (min-width: 47.9375em) {
  .nav__checkbox {
    display: none;
  }
}
.nav__toggle {
  display: flex;
  flex-direction: column;
  margin-top: 0.375rem;
}
@media (min-width: 47.9375em) {
  .nav__toggle {
    display: none;
  }
}
.nav__line {
  background: var(--very-dark-blue);
  border-radius: 5px;
  height: 0.1875rem;
  margin: 0.1875rem auto;
  opacity: 1;
  position: relative;
  width: 2rem;
  transition: all 0.5s cubic-bezier(0.8, -0.01, 0.12, 0.99);
  z-index: 2;
}
.nav__line:first-child {
  transform-origin: 0% 0%;
}
.nav__line:nth-child(3) {
  transform-origin: 0% 100%;
}
.nav__container {
  background: var(--off-white);
  height: 100vh;
  width: 65vw;
  position: absolute;
  left: 100vw;
  top: 0;
  transition: transform 0.5s ease-in-out;
  z-index: 1;
}
@media (min-width: 47.9375em) {
  .nav__container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
    width: 31.25rem;
    position: static;
    transform: none;
    transition: none;
  }
}
.nav__navlist {
  margin-top: 8.125rem;
  padding-left: 1.875rem;
}
@media (min-width: 47.9375em) {
  .nav__navlist {
    display: flex;
    margin: 0;
    padding: 0;
  }
}
.nav__navlink {
  padding-bottom: 1.875rem;
}
@media (min-width: 47.9375em) {
  .nav__navlink {
    padding-left: 2.25rem;
    padding-bottom: 0;
  }
}
.nav__navlink a {
  color: var(--very-dark-blue);
  cursor: pointer;
}
.nav__navlink a:hover {
  color: var(--soft-red);
}

.hero {
  width: 100%;
}
.hero__img {
  object-fit: contain;
  margin-bottom: 1.125rem;
  width: 100%;
}
@media (min-width: 47.9375em) {
  .hero__img {
    margin-bottom: 1.5rem;
  }
}
.hero__headline {
  display: flex;
  flex-direction: column;
}
@media (min-width: 47.9375em) {
  .hero__headline {
    flex-direction: row;
  }
}
@media (min-width: 47.9375em) {
  .hero h1 {
    max-width: 350px;
  }
}
@media (min-width: 47.9375em) {
  .hero__content {
    margin-left: 2.5rem;
  }
}
.hero__content p {
  color: var(--dark-grayish-blue);
  line-height: 1.7;
  margin: 0 auto 1.25rem;
}
.hero__btn {
  background: var(--soft-red);
  border: none;
  color: var(--off-white);
  cursor: pointer;
  font-size: 0.75rem;
  letter-spacing: 0.25rem;
  padding: 0.9375rem 1.875rem;
  text-transform: uppercase;
  transition: background 0.2s ease-in-out;
}
.hero__btn:hover {
  background: var(--very-dark-blue);
}